<script lang="ts">
    import { onMount } from "svelte";
    import { _ } from "svelte-i18n";
    import MarkdownText from "../../utils/MarkdownText.svelte";

    let codeContainer;

    export let codePath: string;
    export let title: string;
    export let description: string;

    export let left: boolean = false;

    onMount(() => {
        if (codePath != null && codePath != undefined) {
            fetch("/assets/demos/" + codePath)
                .then((result) => {
                    return result.text();
                })
                .then((data) => {
                    // Warning, be careful what we put inside demos as it can lead to XSS!!
                    // Script tags for ex. will not be extecuted but like the img onerror will be!
                    codeContainer.textContent = data;
                });
        }
    });
</script>

{#if left}
    <div class="row center-xs">
        <div class="col-md-1 col-xs-12" />
        <div class="col-md-6 col-xs-12" data-aos="zoom-in" data-aos-delay="500">
            <pre ><code class="language-java match-braces" bind:this={codeContainer}
                    >Loading demo...</code
                ></pre>
        </div>
        <div class="col-md-4 col-xs-12 middle-xs al" data-aos="fade-left" data-aos-delay="500">
            <h2><MarkdownText translationKey={title} /></h2>
            <p><MarkdownText translationKey={description} /></p>
        </div>
    </div>
{:else}
    <div class="row center-xs">
        <div class="col-md-4 col-xs-12 middle-xs" data-aos="fade-right" data-aos-delay="500">
            <h2 class="ar"><MarkdownText translationKey={title} /></h2>
            <p class="al"><MarkdownText translationKey={description} /></p>
        </div>
        <div class="col-md-6 col-xs-12" data-aos="zoom-in" data-aos-delay="500">
            <pre><code class="language-java match-braces" bind:this={codeContainer}
                    >Loading demo...</code
                ></pre>
        </div>
        <div class="col-md-1 col-xs-12" />
    </div>
{/if}

<style>
    .row {
        margin-bottom: 10rem;
    }

    .al {
        text-align: left;
    }

    .ar {
        text-align: right;
    }

    @media only screen and (max-width: 1030px) {
        .row h2 {
            text-align: center;
        }

        .row p {
            text-align: center;
        }
    }
</style>
