<script>
    import { onMount } from "svelte";
    import { _ } from "svelte-i18n";

    let typer;

    onMount(() => {
        typer.innerHTML = $_("hero_typer_start").replaceAll("%typer%", '<div class="typed" style="display: inline;">');

        var options = {
            strings: JSON.parse($_("hero_typer")),
            typeSpeed: 30,
            backSpeed: 40,
            shuffle: true,
            backDelay: 5000,
            loop: true,
        };
        var typed = new Typed(".typed", options);
    });
</script>

<section class="hero" id="hero" style="margin-top: 3rem;">
    <div class="row center-xs">
        <div class="col-xs-6">
            <div class="box">
                <h1 class="title">Icicle</h1>
                <h2 class="subtitle" id="typer-start" bind:this={typer}>
                    <div
                        class="typed"
                        style="display: inline;"
                    />
                </h2>
            </div>
            <div class="row start-xs" style="margin-top: 3rem;">
                <!-- TODO: Replace GitBook URLs once the actual, up-to-date documentation is done -->

                <div class="col-xs-12 col-md-4 mt">
                    <div class="row center-xs">
                        <a
                            id="hero-why"
                            class="button"
                            href="https://docs.iceyleagons.net/icicle/"
                            >{$_("hero_why")}</a
                        >
                    </div>
                </div>
                <div class="col-xs-12 col-md-4 mt">
                    <div class="row center-xs">
                        <a
                            id="hero-gh"
                            class="button"
                            href="https://github.com/IceyLeagons/iciclev2"
                            target="_blank">{$_("hero_github")}</a
                        >
                    </div>
                </div>
                <div class="col-xs-12 col-md-4 mt">
                    <div class="row center-xs">
                        <a
                            id="hero-start"
                            class="button"
                            href="https://docs.iceyleagons.net/icicle/"
                            >{$_("hero_start")}</a
                        >
                    </div>
                </div>
            </div>
        </div>
    </div>
</section>

<style>
    .hero {
        padding-top: 6rem;
        padding-bottom: 6rem;
        color: var(--text-color);

        margin-top: var(--nav-height);

        background: var(--card-color) var(--bg-img) no-repeat fixed center
            center;
        background-size: cover;

        border-bottom: solid 5px var(--nav-border);
    }

    .hero .box {
        padding: 2px;
        border-radius: 10px;
        background: var(--hero-box);
