<script lang="ts">
    import { onMount } from "svelte";

    import { _ } from "svelte-i18n";
    import links from "../configs/navbar.json";

    let mobileMenu;

    onMount(() => {
        toggleNav();
        detectColorScheme();
    });

    function toggleNav() {
        mobileMenu.classList.toggle("visible");
    }

    function detectColorScheme() {
        var theme = "light"; //default to light

        //local storage is used to override OS theme settings
        if (localStorage.getItem("theme")) {
            if (localStorage.getItem("theme") == "dark") {
                var theme = "dark";
            }
        } else if (!window.matchMedia) {
            //matchMedia method not supported
            return false;
        } else if (window.matchMedia("(prefers-color-scheme: dark)").matches) {
            //OS theme setting detected as dark
            var theme = "dark";
        }

        //dark theme preferred, set document with a `data-theme` attribute
        if (theme == "dark") {
            document.documentElement.setAttribute("data-theme", "dark");
        }
    }

    //identify the toggle switch HTML element
    let toggleSwitch;
    let toggleSwitch2;

    //function that changes the theme, and sets a localStorage variable to track the theme between page loads
    function switchTheme(e) {
        if (e.target.checked) {
            localStorage.setItem("theme", "dark");
            document.documentElement.setAttribute("data-theme", "dark");
            toggleSwitch.checked = true;
            toggleSwitch2.checked = true;
        } else {
            localStorage.setItem("theme", "light");
            document.documentElement.setAttribute("data-theme", "light");
            toggleSwitch.checked = false;
            toggleSwitch2.checked = false;
        }
    }

    //pre-check the dark-theme checkbox if dark-theme is set
    if (document.documentElement.getAttribute("data-theme") == "dark") {
        toggleSwitch.checked = true;
        toggleSwitch2.checked = true;
    }
</script>

<nav class="navbar">
    <a href="#hero" class="skip-to-content">Skip navbar</a>
    <div class="row between-xs">
        <div class="col-xs-3 col-lg-3">
            <img
                alt="logo"
                class="logo"
                src="assets/imgs/black_no_subtitle.png"
            />
        </div>
        <div class="col-xs-1 col-lg-4" />
        <div
            class="col-xs-8 col-lg-5"
            style="margin-top: 1.3rem; text-align: right;"
        >
            <!-- TODO: make this sh*t responsive -->
            {#each links as link}
                <a
                    id={link.id}
                    class="nav-link nav-link-collapse"
                    href={link.href}
                    on:click={toggleNav}>{$_(link.i18n)}</a
                >
            {/each}

            <a
                class="nav-resp-collapse nav-link"
                href="#"
                on:click={toggleNav}>&#9776;</a
            >

            <label
                id="theme-switch"
                class="theme-switch nav-link nav-link-collapse"
                for="checkbox_theme"
            >
                <i class="fa-solid fa-circle-half-stroke" />
                <input type="checkbox" id="checkbox_theme" on:change={switchTheme} bind:this={toggleSwitch} />
            </label>
        </div>
    </div>
    <div
        class="nav-collapsed visible"
        id="responsive-nav-menu"
        bind:this={mobileMenu}
    >
        {#each links as link}
            <a
                id={link.id}
                class="nav-link "
                href={link.href}
                on:click={toggleNav}>{$_(link.i18n)}</a
            >
        {/each}
        <label
            id="theme-switch2"
            class="theme-switch nav-link"
            for="checkbox_theme"
        >
            <i class="fa-solid fa-circle-half-stroke" />
            <input type="checkbox" id="checkbox_theme" on:change={switchTheme} bind:this={toggleSwitch2} />
        </label>
    </div>
</nav>

<style>
    .skip-to-content {
        position: fixed;
        left: 10rem;
        background-color: black;
        color: white;
        z-index: 99;
        font-size: 1rem;

        transform: translateY(-100%);
        transition: .3s;
    }

    .skip-to-content:focus {
        transform: translateY(0%);
    }

    .navbar {
        position: fixed;
        z-index: 20;

        top: 0px;
        left: 0px;

        width: 100%;
        height: var(--nav-height);
        background-color: var(--card-color);

        border-bottom: solid 5px var(--nav-border);
    }

    .navbar .logo {
        width: 250px;
    }

    .navbar .nav-link {
        color: var(--text-color);
        margin: 1rem;
        text-decoration: none;
        text-align: center;
    }

    .navbar .nav-link:hover {
        color: #3e9ce3;
    }

    .theme-switch {
        text-align: center;
        cursor: pointer;
    }

    .theme-switch i {
        cursor: pointer;
    }

    .theme-switch input {
        opacity: 0;
        cursor: pointer;
    }

    .navbar .nav-resp-collapse {
        display: none;
        color: var(--text-color);
        margin: 1rem;
        text-decoration: none;
        text-align: center;
    }

    .navbar .nav-collapsed {
        display: none;
        flex-direction: column;
        background: var(--bg-color);
        z-index: 999;
    }

    @media screen and (max-width: 900px) {
        .navbar .nav-collapsed.visible {
            display: flex;
        }

        .navbar .nav-resp-collapse {
            position: absolute;
            display: unset;

            top: 0.5rem;
            right: 1rem;
        }

        .navbar .nav-link-collapse {
            display: none;
        }
    }
</style>
