<script>
	import { _ } from "svelte-i18n";
	import { fly } from "svelte/transition";
	import { Modals, closeModal } from "svelte-modals";
	import { clickOutside } from "./utils/outsideClick";

	import Award from "./components/Award.svelte";
	import Demo from "./components/demo/Demo.svelte";
	import Features from "./components/features/Features.svelte";
	import Hero from "./components/Hero.svelte";
	import Navbar from "./components/Navbar.svelte";
	import { registerLocalizer, languages } from "./i18n/localizer";
	import MarkdownText from "./utils/MarkdownText.svelte";
	import { onMount } from "svelte";

	let languagesVisible;

	registerLocalizer();
	let currentLanguage = localStorage.getItem('lang');
	let currentFlag = getFlagFromKey(currentLanguage);

	function getFlagFromKey(key) {
		for (let i = 0; i < languages.length; i++) {
			if (languages[i].key == key) return languages[i].flag;
		}
		return undefined;
	}

	onMount(() => {
		AOS.init();
	});
</script>
<svelte:head>
	{#each languages as language}
		<meta property="og:locale:alternate" content="{language.key}_{language.key.toLocaleUpperCase()}">
	{/each}
</svelte:head>

<Navbar />
<Hero />
<Features />
<Award />
<Demo />

<section class="else" style="padding-bottom: 10rem;">
	<div class="row center-xs">
        <div class="col-xs-12 col-md-6">
            <h1 class="title" data-aos="flip-up">{$_("footer_title")}</h1>
			<h2 class="subtitle" data-aos="flip-donw">{$_("footer_subtitle")}</h2>

			<div class="row">
				<div class="col-xs-12 col-md-6" data-aos="fade-right" style="margin-top: 4rem;">
					<a href="#" class="button">{$_("footer_github_button")}</a>
				</div>
				<div class="col-xs-12 col-md-6" data-aos="fade-left" style="margin-top: 4rem;">
					<a href="#" class="button">{$_("footer_documentation_button")}</a>
				</div>
			</div>
        </div>
    </div>
</section>

<footer
	class="footer"
	style="position: absolute; left: 50%; transform: translateX(-50%); display: flex; width: 100%;"
>
	<div id="copyright" class="footer-content" style="margin-left: 1rem;">
		<MarkdownText translationKey="copyright" />
	</div>

	<!--<a href="#">Go to top</a>-->

	<div class="language-select" style="margin-right: 1rem;" >
		{#if languagesVisible}
			<div class="languages hidden" style="overflow-y: scroll;" transition:fly use:clickOutside on:outclick={() => {
				languagesVisible = !languagesVisible;
			}}>
				{#each languages as language}
					{#if language.key != currentLanguage}
						<span class="fi fi-{language.flag} lang" on:click={() => {
							localStorage.setItem('lang', language.key);
							languagesVisible = false;
							history.go(0);
						}}/>
					{/if}
				{/each}
			</div>
		{/if}
		<span
			class="fi fi-{currentFlag} lang" tabindex="0" title="Change language" id="language-select" style="margin-top: 15px;"
			on:click={() => {
				languagesVisible = !languagesVisible;
			}}
		/>
	</div>
</footer>

<Modals>
	<div slot="backdrop" class="backdrop" on:click={closeModal} />
</Modals>

<style>
	.else {
		background-color: var(--accent-color);
	}

	.footer {
		background-color: var(--footer-color);
	}

	.language-select .fi {
		font-size: 2rem;
	}

	.language-select {
		position: absolute;
		right: 0px;
		display: inline-block;
	}

	.language-select .languages {
		z-index: 555;
		position: fixed;
		float: right;

		bottom: 100px;
		width: 100%;

		height: auto;
		margin-bottom: -2.5rem;

		display: flex;
		flex-direction: column-reverse;
		justify-content: flex-end;
	}

	.lang {
		margin-top: 10px;
		cursor: pointer;
		transition: .5s transform;
	}

	.lang:hover {
		transform: translateY(-3px);
	}

	.language-select .languages span {
		margin-bottom: .8rem;
	}

	.backdrop {
		z-index: 998;
		position: fixed;
		top: 0;
		bottom: 0;
		right: 0;
		left: 0;
		background: rgba(0, 0, 0, 0.5);
	}
</style>