import { addMessages, init, getLocaleFromNavigator } from 'svelte-i18n';
import en from './en.json';
import hu from './hu.json';
import './de.json';
let languages = [
    {
        key: 'en',
        component: en,
        flag: 'gb'
    },
    {
        key: 'hu',
        component: hu,
        flag: 'hu'
    },
];
/*
    {
        key: 'de',
        component: de,
        flag: 'de'
    }
*/
function registerLocalizer() {
    languages.forEach((lang) => {
        addMessages(lang.key, lang.component);
    });
    if (localStorage.getItem('lang') === null) {
        localStorage.setItem('lang', getLocaleFromNavigator().split("-")[0]);
    }
    init({
        fallbackLocale: 'en',
        initialLocale: localStorage.getItem('lang')
    });
}
export { registerLocalizer, languages };
